* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html,
body {
	background: #000 url('img/bg.jpg') repeat center center/cover;
	height: 100vh;
	color: #fff;
	font-family: Arial, Helvetica, sans-serif;
}

a {
	text-decoration: none;
}

.container {
	/* max-width: 1100px; */
	margin-top: 2rem;
	padding: 50px 20px;
	display: grid;
	grid-template-columns: repeat(4, 4fr);
	gap: 1rem;
}

.block {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
}

.btn {
	display: inline-block;
	color: #fff;
	background-color: #3fb573;
	font-size: 1em;
	text-align: center;
	padding: 10px 15px;
	border: 0;
	margin: 10px 0;
}

header {
	height: 200px;
}

header img {
	width: 200px;
}
.logo {
	padding-top: 1rem;
	margin: 1rem;
}

.center {
	display: flex;
	align-items: center;
	justify-content: center;
}

.search {
	height: 100px;
}

input[type='text'] {
	display: block;
	padding: 10px;
	font-size: 20px;
	border: 0;
	border-radius: 5px;
	width: 30%;
	margin: auto;
	outline: none;
	margin-top: 2rem;
}

.cards {
	display: grid;
	grid-template-columns: repeat(4, 4fr);
	gap: 1rem;
}

.card {
	cursor: pointer;
	background-color: transparent;
	height: 300px;
	width: 300px;
	perspective: 1000px;
}

.card:hover {
	background-color: #5f7821;
}

.suggest {
	cursor: pointer;
}
.suggest:hover {
	color: black;
}

.card h1 {
	font-size: 25px;
	border-bottom: 1px #fff solid;
	padding-bottom: 10px;
	margin-bottom: 10px;
}

.card img {
	width: 100%;
	height: 300px;
	object-fit: cover;
}

.signout {
	width: 100px;
	height: 100px;
	float: right;
	margin-right: 3rem;
	margin-top: 2rem;
}
.card-inner {
	position: relative;
	width: 100%;
	height: 100%;
	transition: transform 0.8s;
	transform-style: preserve-3d;
}

.card:hover .card-inner {
	transform: rotateY(180deg);
}

.card-front,
.card-back {
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.card-back {
	background-color: #333;
	color: white;
	padding: 20px;
	transform: rotateY(180deg);
}

.card li {
	list-style: none;
	padding-bottom: 10px;
}

@media (max-width: 800px) {
	.container {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (max-width: 500px) {
	.container {
		grid-template-columns: 1fr;
	}
	.card img {
		width: 100%;
		height: 300px;
		object-fit: contain;
	}
}
